.q-bar{
  max-height: 80vh;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 5px;
    background-color: transparent;
  }
  &::-webkit-scrollbar-track {
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background-color: dimgrey;
    border-radius: 5px;
  }
  .list-group-item{
    padding:15px;
  }
  li.active-q{
    background: #888;
  }
  a{
    color: #fff;
  }
}
